body {
  margin: 0;
  padding: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  text-align: center;
}

* {
  box-sizing: border-box;
}
.dropzone-field {
    width: 100%;
}

.dropzone {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 350px;
    width: 100%;
    height: 270px;
    border: 1px solid black;
    border-radius: 4px;
    padding: 15px;
    margin: 1rem auto;
}

.preview-image {
    max-width: 100%;
    max-height: 100%;
}
.task-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
    max-width: 650px;
    margin: 0 auto;
    border: 2px solid #000;
    padding: 1.5rem;
    border-radius: 5px;
}

.input-field {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 100%;
    margin: 0 auto;
    text-align: left;
    border-bottom: 2px solid #000;
}

.input {
    display: block;
    border: none;
    outline: none;
}

.textarea {
    min-height: 4rem;
    display: block;
    border: none;
    outline: none;
    margin-bottom: 2px;
}

.btn {
    display: block;
    border: 2px solid #00aeff;
    border-radius: 4px;
    padding: 0.5rem;
    color: #fff;
    background: #00aeff;
    outline: none;
    font-weight: 700;
    cursor: pointer;
}

.btn-inactive {
    background: #fafafa;
    color: #00aeff;
}
.task-item {
    width: 90%;
    max-width: 500px;
    margin: 1rem auto;
    border: 2px solid #000;
    border-radius: 4px;
    padding: 1rem;
    position: relative;
}

.task-item-avtive {
    background: #E3F2FD;
}

.loader-container {
    display: flex;
    justify-content: flex-end;
    width: 2.3rem;
    height: 2.3rem;
    border: 2px solid #00aeff;
    border-radius: 4px;
    margin-left: auto;
}

.task-user-info {
    display: flex;
    justify-content: space-between;
}

.task-name {
    font-weight: 700;
    font-size: 1.2rem;
}

.task-email {
    font-style: italic;
    font-size: 1.2rem;
}

.task-text {
    font-weight: 200;
    padding: 1rem;
    text-align: left;
}

.task-text-edit {
    display: block;
    width: 100%;
    margin-top: 1rem;
    padding: 1rem;
}

.task-image {
    max-width: 30%;
    margin: 0.5rem;
}

.task-edit-block {
    display: flex;
    justify-content: flex-end;
}

.btn-edit {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 2.3rem;
    height: 2.3rem;
    color: #00aeff;
    background: #fff;
    border: 2px solid #00aeff;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    margin: 0 5px;
}

.checkbox {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 2.3rem;
    height: 2.3rem;
    color: #00aeff;
    background: #fff;
    border: 2px solid #00aeff;
    border-radius: 4px;
    position: relative;
    margin: 0 5px;
}

.checkbox input {
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0;
    opacity: 0;
    z-index: 2;
    cursor: pointer;
}

.check-icon {
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
}

.err-msg {
    position: absolute;
    top: 1rem;
    left: 1rem;
    color: red;
}
.page-nav-container {
    margin: 1rem;
    font-size: 1.2rem;
}

.page-num {
    padding: 0.4rem;
    cursor: pointer;
    font-style: italic;
}

.page-num-active {
    font-size: 1.5rem;
    border-bottom: 2px solid #000;
}
.login-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: 0.5rem;
}

.loged-in-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.loged-out-block {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.btn-login {
    margin-left: 1rem;
}

.input-field-login {
    margin-right: 1rem;
    max-width: 70px;
    border-bottom: 3px solid #00aeff;
}
